import './App.css';
import "bootstrap/dist/css/bootstrap.min.css";
import AddEntry from './components/AddEntry';
import AddBudgetLine from "./components/AddBudgetLine";
import ListEntries from "./components/ListEntries";
import ListBudget from "./components/ListBudget";
import Configuration from "./components/Configuration";
import Home from "./components/Home";
import { Switch, Route, Link } from "react-router-dom";
import Navbar from "react-bootstrap/Navbar";
import Nav from "react-bootstrap/Nav";



function App() {

  return (
    <div className="App">
      <Navbar bg="dark" variant="dark" sticky="top" expand="md">
        <Navbar.Brand><Link to={"/presupuestador-front"}>/Presupuestaré/
        </Link></Navbar.Brand>
      <Nav className="mr-auto">
      <Link to={"presupuestador-front/entries"} className="nav-link">Listar movimientos
      </Link>
      <Link to={"presupuestador-front/add"} className="nav-link">Sumar movimiento
      </Link>
      <Link to={"presupuestador-front/addBudget"} className="nav-link">Sumar categoría de movimiento
      </Link>
      <Link to={"presupuestador-front/budget"} className="nav-link">Presupuesto
      </Link>
      <Link to={"presupuestador-front/config"} className="nav-link">Configuración
      </Link>
        </Nav>
      </Navbar>
 
      <div>
      <Switch>
        <Route exact path="presupuestador-front/budget" component={ListBudget} />
        <Route exact path="presupuestador-front/entries" component={ListEntries} />
        <Route exact path="presupuestador-front/add" component={AddEntry} />
        <Route exact path="presupuestador-front/addBudget" component={AddBudgetLine} />
        <Route exact path="presupuestador-front/config" component={Configuration} />
        <Route exact path="/presupuestador-front" component={Home} />

      </Switch>
      </div>
  

   </div>
  );
}

export default App;
